<template>
    <div class="login_page">
        <div class="login_area">
            <div class="login_title">长运集团公司</div>
            <div class="login_title">用户登录</div>
            <div class="login_usr">
                <el-input placeholder="请输入账号"
                          v-model="userID"
                          prefix-icon="el-icon-user"
                          clearable>
                    <template slot="prepend">账号</template>
                </el-input>
            </div>
            <div class="login_psd">
                <el-input placeholder="请输入密码"
                          v-model="password"
                          prefix-icon="el-icon-lock"
                          show-password>
                    <template slot="prepend">密码</template>
                </el-input>
            </div>
            <div class="login_btn">
                <el-button type="primary" style="width: 100%;" @click="login()">登录</el-button>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "login",
        data() {
            return {
                userID: "",
                password: "",
                isShowErr: false,
            }
        },
        methods: {
            login: function () {
                //登录验证
                if (this.userID == "admin" && this.password == "admin") {
                    this.$router.push({path: '/personalInfo', params: {id: '1111'}})
                } else {
                    this.userID = '';
                    this.password = "";
                    this.$message({
                        message: '账号密码错误,重新输入 ',
                        type: 'error',
                        duration: 1000,
                    })
                }
            }
        }
    };
</script>

<style lang="scss">
    .login_page {
        width: 100%;
        height: 100vh;
        min-width: 600px;
        min-height: 400px;
        background: url("../../static/img/img_login.png") no-repeat;
        display: flex;
        justify-content: center;
        align-items: center;
        .el-icon-user,.el-icon-lock {
            color: #82c5ff;
        }
        .el-input-group__prepend{
            color: #010107;
        }
    }

    .login_area {
        width: 500px;
        height: 300px;
    }

    .login_title {
        height: 40px;
        line-height: 40px;
        color: white;
        text-align: center;
    }

    .login_usr, .login_psd, .login_btn {
        width: 80%;
        padding: 30px 10% 0 10%;
    }


</style>



